var cameraNftSphere, controlsNftSphere, canvasNftSphere, sceneNftSphere, rendererNftSphere, modelNftSphere, checkNftSphere =1;
let mixerNftSphere;
var clockNftSphere = new THREE.Clock();


function initNftSphere(){
    
    canvasNftSphere = document.querySelector('.sphereNft');
    sceneNftSphere = new THREE.Scene();
    const sizes = {
        width: (window.innerWidth/4.5),
        height: (window.innerHeight/4)
    }; 
    // mesh
    const geometry = new THREE.SphereGeometry(8,64,64);
    const material = new THREE.MeshPhysicalMaterial({
        roughness: 1,
        metalness: 0,
        transmission: 0
        
    });
    modelNftSphere = new THREE.Mesh(geometry,material);

    sceneNftSphere.add(modelNftSphere);

    // camera
    cameraNftSphere = new THREE.PerspectiveCamera(
        75,
        sizes.width / sizes.height,
        0.1,
        1000
    );
    cameraNftSphere.position.z = 15;
    sceneNftSphere.add(cameraNftSphere);

    // lights
    let ambientLight = new THREE.AmbientLight(new THREE.Color('hsl(0, 0%, 100%)'), 0.05);
    sceneNftSphere.add(ambientLight);

    const lightWhite = new THREE.PointLight(0xffffff, 1.3, 100);
    lightWhite.position.set(10,0,10);
    sceneNftSphere.add(lightWhite);

    const lightOrange = new THREE.PointLight(0x994908, 1.5, 100);
    lightOrange.position.set(0,10,10);
    sceneNftSphere.add(lightOrange);

    const lightYellow = new THREE.PointLight(0xffd633, 1, 100);
    lightYellow.position.set(10,10,0);
    sceneNftSphere.add(lightYellow);

    // controls

    controlsNftSphere = new OrbitControls(cameraNftSphere,canvasNftSphere);
    controlsNftSphere.enableDamping = true;
    controlsNftSphere.enablePan = false;
    controlsNftSphere.enableZoom = false;
    controlsNftSphere.autoRotate = true;
    
    controlsNftSphere.autoRotateSpeed = 3;
    
    // renderer
       
    rendererNftSphere = new THREE.WebGLRenderer({
        canvas: canvasNftSphere,
        alpha: true
    });
    rendererNftSphere.setSize(sizes.width,sizes.height);
    rendererNftSphere.render(sceneNftSphere,cameraNftSphere);
    rendererNftSphere.setPixelRatio(Math.min(window.devicePixelRatio,2));
}
initNftSphere();
window.addEventListener('resize',function(){
    if(cameraNftSphere){
        cameraNftSphere.aspect = (window.innerWidth/4.5) / (window.innerHeight/4);
        modelNftSphere.position.normalize();
        cameraNftSphere.updateProjectionMatrix();
    }
    
    rendererNftSphere.setSize((window.innerWidth/4.5),(window.innerHeight/4));
    rendererNftSphere.setPixelRatio(Math.min(window.devicePixelRatio,2));
});
function animateNftSphere(){
    const elapsedTime = clockNftSphere.getElapsedTime();
    if(modelNftSphere){
        modelNftSphere.position.y = Math.sin(elapsedTime * 0.5)*0.1 - 0.1;
        controlsNftSphere.update();
        // console.log("hi");
        // console.log(modelNftSphere.position);
    }
    
    rendererNftSphere.render(sceneNftSphere,cameraNftSphere);
}
rendererNftSphere.setAnimationLoop(animateNftSphere);

const inAnimation = gsap.timeline({defaults: {duration: 1}});
inAnimation.fromTo(modelNftSphere.scale, {z:0, x:0, y:0}, {z:1, x:1, y:1});

