var cameraModelGame, canvasModelGame, sceneModelGame, rendererModelGame, controlsModelGame;
var smartphoneModelGame, charModelGame1, charModelGame2;
let mixerModelGame;

var prevTimeModelGame = 0;
function animateModelGame(){
    const elapsedTime = clock.getElapsedTime();
    controlsModelGame.update();
    // loading is an sync function so wait for the the model to load
    if(smartphoneModelGame && charModelGame1 && charModelGame2){
        // console.log("HI from animate");
        var delta = elapsedTime - prevTimeModelGame;
        prevTimeModelGame = elapsedTime;
        mixerModelGame.update(delta);

        var x = (Math.sin(elapsedTime))*0.0005;
   
        smartphoneModelGame.children[0].position.y += x;
        charModelGame1.children[0].position.y += x;
        charModelGame2.children[0].position.y += x;
        // cameraModelGame.lookAt(smartphoneModelGame.position);
    }
        
    rendererModelGame.render(sceneModelGame,cameraModelGame);
}

function initModelGame(){
    console.log("hello")
    var importedModel = new URL('../static/assets/Mobile_Phone.glb', import.meta.url);
    var importedModel2 = new URL('../static/assets/Tan_Nin.glb', import.meta.url);
    var importedModel3 = new URL('../static/assets/Tan_Nin2.glb', import.meta.url);
    
    var sizes = {
        width: window.innerWidth,
        height: window.innerHeight
    };

    // Scene
    sceneModelGame = new THREE.Scene();

    canvasModelGame = document.querySelector('.ModelGame');
    
    rendererModelGame = new THREE.WebGLRenderer({
        canvas: canvasModelGame,
        antialias: true,
        alpha: true
    });

    rendererModelGame.setSize(sizes.width,sizes.height - 105);
    // rendererModelGame.setClearColor(0xA3A3A3);

    // Camera
    cameraModelGame = new THREE.PerspectiveCamera(
        75,
        sizes.width / sizes.height,
        0.1,
        1000
    );
    cameraModelGame.position.set(0,1,2);
    cameraModelGame.lookAt(new THREE.Vector3(0,1,0));
    // Controls
    controlsModelGame = new OrbitControls(cameraModelGame,rendererModelGame.domElement);

    // controlsModelGame.enableDamping = true;
    // controlsModelGame.enablePan = false;

    // Axes Helper and grid
    // const grid = new THREE.GridHelper(30,30);
    // sceneModelGame.add(grid);
    // sceneModelGame.add( new THREE.AxesHelper());

    // lights
    var light = new THREE.AmbientLight(0x111);
    sceneModelGame.add(light);
    let ambientLight = new THREE.AmbientLight(new THREE.Color('hsl(0, 0%, 100%)'), 0.75);
    sceneModelGame.add(ambientLight);
    
    let directionalLightBack = new THREE.DirectionalLight(new THREE.Color('hsl(0, 0%, 100%)'), 1);
    directionalLightBack.position.set(30, 100, 100);
    sceneModelGame.add(directionalLightBack);

    let directionalLightFront = new THREE.DirectionalLight(new THREE.Color('hsl(0, 0%, 100%)'), 0.75);
    directionalLightFront.position.set(-30, 100, -100);
    sceneModelGame.add(directionalLightFront);
    const assetLoader = new GLTFLoader();

    // const near = 0;
    // const far = 2;
    // const color = 'white';
    // sceneModelGame.fog = new THREE.Fog( color, near, far );
    // sceneModelGame.background = new THREE.Color( color );
//   sceneModelGame.fog = new THREE.Fog(color, near, far);
//////////////////////////////////////////////////////////////////
//Loading the assets
//////////////////////////////////////////////////////////////////

    Promise.all([
        assetLoader.loadAsync(importedModel.href),
        assetLoader.loadAsync(importedModel2.href),
        assetLoader.loadAsync(importedModel3.href)
    ]).then(models => {
        // get what you need from the models array
        smartphoneModelGame = models[0].scene;
        charModelGame1 = models[1].scene;
        charModelGame2 = models[2].scene;
      
//////////////////////////////////////////////////////////////////
//Loading the smartphoneModelGame
//////////////////////////////////////////////////////////////////
        smartphoneModelGame.children[0].position.set(0,0,-5);
        smartphoneModelGame.children[0].scale.x = 0; 
        smartphoneModelGame.children[0].scale.y = 0; 
        smartphoneModelGame.children[0].scale.z = 0; 
        smartphoneModelGame.children[0].rotation.x=-1.5707963267948963;
        smartphoneModelGame.children[0].rotation.y=0;
        smartphoneModelGame.children[0].rotation.z=0;

        // add model to the scene
        sceneModelGame.add(smartphoneModelGame);
        mixerModelGame = new THREE.AnimationMixer(smartphoneModelGame);
        // To Play Model's animation 

        // To laod a single animation frrom the model       
        // const clips = gltf.animations;
        // const clip = THREE.AnimationClip.findByName(clips,'');
        // const action = mixer.clipAction(clip);
        // action.play();

        // const clips = gltfModelGame.animations;

        // clips.forEach(function(clip){
        //     const action = mixerModelGame.clipAction(clip);
        //     action.play();
        // });

//////////////////////////////////////////////////////////////////
//Loading the CharacterModelGame1
//////////////////////////////////////////////////////////////////

        charModelGame1.children[0].position.set(0.3,-3,1.5);
        charModelGame1.children[0].scale.x = 0; 
        charModelGame1.children[0].scale.y = 0; 
        charModelGame1.children[0].scale.z = 0; 
        charModelGame1.children[0].rotation.x=Math.PI / 2;
        charModelGame1.children[0].rotation.y=0;
        charModelGame1.children[0].rotation.z=1.22173048;

        // add model to the scene
        sceneModelGame.add(charModelGame1);

//////////////////////////////////////////////////////////////////
//Loading the CharacterModelGame2
//////////////////////////////////////////////////////////////////

        charModelGame2.children[0].position.set(-0.3,-0.3,1.5);
        charModelGame2.children[0].scale.x = 0; 
        charModelGame2.children[0].scale.y = 0; 
        charModelGame2.children[0].scale.z = 0; 
        charModelGame2.children[0].rotation.x=Math.PI / 2;
        charModelGame2.children[0].rotation.y=0;
        charModelGame2.children[0].rotation.z=-1.22173048;

        // add model to the scene
        sceneModelGame.add(charModelGame2);
//////////////////////////////////////////////////////////////////
//Setting up gsasp scroll trigger
//////////////////////////////////////////////////////////////////
        const tl = gsap.timeline({
            scrollTrigger: {
                trigger: '.ModelGameDiv',
                start: 'top center',
                end: 'top center',
                ease: "power2.inOut",
                // scrub: true,
                toggleActions: "play none none reverse",
                immediateRender: false
            }
                
        });
        
        tl.to(smartphoneModelGame.children[0].scale, {duration:3, ease: 'power2.inOut', z:10, x:7, y:7},0)
        // .to(smartphoneModelGame.children[0].rotation, {duration:3, ease: 'power2.inOut', z:7.8, x:1.7, y:-1.57079633},0)
        .to(smartphoneModelGame.children[0].rotation, {duration:3, ease: 'power2.inOut', z:0, x:-2.77079633, y:1.57079633},0)
        .to(smartphoneModelGame.children[0].position, {duration:2, ease: 'power2.inOut', z:0.5, x:-0.8, y:0.5},0)
        .to(charModelGame1.children[0].position,{duration:3, ease: 'power2.inOut', z:0.8, x:0.3, y:0.5},"<")
        .to(charModelGame2.children[0].position,{duration:3, ease:'power2.inOut',z:0.8, x:-0.3, y:0.5 },"<")
        .to(charModelGame1.children[0].scale, {duration:1.2, ease: 'power2.inOut', z:0.005, x:0.005, y:0.005},">") 
        .to(charModelGame2.children[0].scale,{duration:1.2, ease:'power2.inOut',z:0.005, x:0.005, y:0.005 },"<");

        console.log("Loaded All");
        rendererModelGame.setAnimationLoop(animateModelGame);
      });   
}

initModelGame();
