        stadiumCompTimeline .children[0].scale.x = 0; 
        stadiumCompTimeline .children[0].scale.y = 0; 
        stadiumCompTimeline .children[0].scale.z = 0; 
        stadiumCompTimeline .children[0].rotation.x=-1.5707963267948963;
        stadiumCompTimeline .children[0].rotation.y=0;
        stadiumCompTimeline .children[0].rotation.z=0;

        // add model to the scene
        sceneCompTimeline .add(stadiumCompTimeline );
        mixerCompTimeline  = new THREE.AnimationMixer(stadiumCompTimeline );
        
        // To Play Model's animation 

        // To laod a single animation frrom the model       
