var cameraCreate, canvas, sceneCreate, rendererCreate, effect, modelCreate, cube, check =1;

function initCreate(){
    
    const importedModel = new URL('../static/assets/Cube_Light.glb', import.meta.url);
    const sizes = {
        width: window.innerWidth,
        height: window.innerHeight
    };
    
    
    rendererCreate = new THREE.WebGLRenderer({ 
        canvas : document.getElementById('NFTAvatarCanvas'), 
        antialias:true, 
        alpha: true 
    });
    rendererCreate.setSize(sizes.width,sizes.height);

    sceneCreate = new THREE.Scene();
    cameraCreate = new THREE.PerspectiveCamera(
        75,
        sizes.width / sizes.height,
        0.1,
        1000
    );

    cameraCreate.position.set(0,0,3);
    cameraCreate.lookAt(new THREE.Vector3(0,0,0));
 
    sceneCreate.add(cameraCreate);
    // //Adds controls to the canvas
    // const orbit = new OrbitControls(cameraCreate,rendererCreate.domElement);
    // orbit.update();
    
    // const grid = new THREE.GridHelper(30,30);
    // sceneCreate.add(grid);

    var light0 = new THREE.AmbientLight(0xffffff,0.8);
    sceneCreate.add(light0);

    const color = 0xFFFFFF;
    const intensity = 0.2;
    const light = new THREE.PointLight(color, intensity);
    light.position.set(0, 10, 0);
    sceneCreate.add(light);
    const color2 = 0xFFFFFF;
    const intensity2 = 1.5;
    const light2 = new THREE.PointLight(color2, intensity2);
    light2.position.set(0, -10, 0);
    sceneCreate.add(light2);

    const assetLoader = new GLTFLoader();
    Promise.all([
        assetLoader.loadAsync(importedModel.href)
    ]).then(models => {
        console.log("Dress Change Model Loaded");
        // get what you need from the models array
        modelCreate = models[0].scene;
        console.log(modelCreate);
        modelCreate.position.set(0,1.1,0);
        modelCreate.rotation.set(0,0.2,0);
        modelCreate.scale.set(0.3,0.3,0.3)

        sceneCreate.add(modelCreate);
        
    });

}

initCreate();

function animateCreate() { 
    if(modelCreate){
        modelCreate.children[0].rotation.y += 0.008;
        modelCreate.children[0].rotation.x += 0.008;
    }
    rendererCreate.render(sceneCreate, cameraCreate);
}
rendererCreate.setAnimationLoop(animateCreate);



